export const isCreator = (
  addr: String,
  setLoading: React.Dispatch<React.SetStateAction<Boolean>>,
  setIsCreator: React.Dispatch<React.SetStateAction<Boolean>>
) => {
  creatorContract.methods
    .balanceOf(addr)
    .call()
    .then((res) => {
      if (res > 0) {
        setIsCreator(true);
      } else {
        setIsCreator(false);
      }
      setLoading(false);
    });
};
